
'use client';
import { useState, useEffect } from 'react';
import type { GalleryImage } from '@/lib/types';
import { Button } from '../ui/button';
import { PlusCircle, Trash2, Loader2 } from 'lucide-react';
import { Card, CardContent } from "@/components/ui/card";
import Image from 'next/image';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
  DialogTrigger,
  DialogFooter,
  DialogClose,
} from "@/components/ui/dialog";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog"

import { Input } from '../ui/input';
import { Label } from '../ui/label';
import { SubmitButton } from '../submit-button';
import { useToast } from '@/hooks/use-toast';
import { fetchGalleryImages } from '@/lib/data';

async function handleGalleryAction(command: 'addGalleryImage' | 'deleteGalleryImage', payload: any) {
    const res = await fetch('/api/vehicle-manager', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ command, ...payload }),
    });

    if (!res.ok) {
        const errorText = await res.text();
        console.error("API Error Response:", errorText);
        try {
            const errorJson = JSON.parse(errorText);
            return { message: `Error: ${res.status} ${res.statusText}`, error: errorJson.message || errorJson.error || "Unknown error" };
        } catch(e) {
            return { message: `Error: ${res.status} ${res.statusText}`, error: errorText };
        }
    }
    
    return await res.json();
}

function GalleryImageForm({ onFormSubmit }: { onFormSubmit: () => void }) {
    const { toast } = useToast();
    const [isOpen, setIsOpen] = useState(false);

    const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const formData = new FormData(event.currentTarget);
        const imageData = {
            description: formData.get('description'),
            imageUrl: formData.get('imageUrl'),
            imageHint: formData.get('imageHint'),
        };

        const result = await handleGalleryAction('addGalleryImage', { imageData });

        if (result.message.includes('success')) {
            toast({ title: 'Success!', description: result.message });
            setIsOpen(false);
            onFormSubmit();
        } else {
            toast({ variant: 'destructive', title: 'Error', description: result.error || result.message });
        }
    };
    
    return (
        <Dialog open={isOpen} onOpenChange={setIsOpen}>
            <DialogTrigger asChild>
                <Button><PlusCircle className="mr-2 h-4 w-4" /> Add New Image</Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-lg">
                 <DialogHeader>
                    <DialogTitle>Add New Gallery Image</DialogTitle>
                    <DialogDescription>
                        Fill out the form to add a new image to the gallery.
                    </DialogDescription>
                </DialogHeader>
                <form onSubmit={handleSubmit} className="space-y-4">
                    <div className="space-y-2"><Label>Description</Label><Input name="description" required /></div>
                    <div className="space-y-2"><Label>Image URL</Label><Input name="imageUrl" required /></div>
                    <div className="space-y-2"><Label>Image Hint (e.g. 'team photo')</Label><Input name="imageHint" required /></div>
                    <DialogFooter>
                        <DialogClose asChild><Button type="button" variant="secondary">Cancel</Button></DialogClose>
                        <SubmitButton label='Add Image' icon={<PlusCircle />} />
                    </DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    );
}

function DeleteImageDialog({ imageId, onDeleted }: { imageId: string, onDeleted: () => void }) {
    const { toast } = useToast();

    const handleDelete = async () => {
        const result = await handleGalleryAction('deleteGalleryImage', { imageId });
        if (result.message.includes('success')) {
            toast({ title: 'Success!', description: 'Image deleted successfully.' });
            onDeleted();
        } else {
            toast({ variant: 'destructive', title: 'Error', description: result.error || result.message });
        }
    };
    
    return (
        <AlertDialog>
            <AlertDialogTrigger asChild>
                <Button variant="destructive" size="sm" className="absolute right-2 top-2 z-10 opacity-80 hover:opacity-100"><Trash2 className="h-4 w-4" /></Button>
            </AlertDialogTrigger>
            <AlertDialogContent>
                <AlertDialogHeader>
                    <AlertDialogTitle>Are you sure?</AlertDialogTitle>
                    <AlertDialogDescription>
                        This action cannot be undone. This will permanently delete the image from your gallery.
                    </AlertDialogDescription>
                </AlertDialogHeader>
                <AlertDialogFooter>
                    <AlertDialogCancel>Cancel</AlertDialogCancel>
                    <AlertDialogAction onClick={handleDelete} className="bg-destructive hover:bg-destructive/90">Delete</AlertDialogAction>
                </AlertDialogFooter>
            </AlertDialogContent>
        </AlertDialog>
    );
}


export default function GalleryManager() {
    const [images, setImages] = useState<GalleryImage[]>([]);
    const [isLoading, setIsLoading] = useState(true);
    
    const refreshData = async () => {
        setIsLoading(true);
        const data = await fetchGalleryImages();
        setImages(data);
        setIsLoading(false);
    };

    useEffect(() => {
        refreshData();
    }, []);

    if (isLoading) {
        return (
            <div className="flex h-64 items-center justify-center">
                <Loader2 className="h-12 w-12 animate-spin text-primary" />
                <p className="ml-4 text-lg">Loading Gallery Data...</p>
            </div>
        );
    }

    return (
        <div className="mt-6">
            <div className="flex justify-end mb-4">
                <GalleryImageForm onFormSubmit={refreshData} />
            </div>
            <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 md:grid-cols-3">
                {images.map((image) => (
                    <Card key={image.id} className="overflow-hidden relative group">
                        <DeleteImageDialog imageId={image.id} onDeleted={refreshData} />
                        <CardContent className="p-0">
                             <div className="relative aspect-video">
                                <Image 
                                    src={image.imageUrl} 
                                    alt={image.description}
                                    fill
                                    className="object-cover w-full h-full"
                                    data-ai-hint={image.imageHint}
                                    sizes="(max-width: 640px) 100vw, (max-width: 768px) 50vw, 33vw"
                                />
                            </div>
                        </CardContent>
                    </Card>
                ))}
            </div>
        </div>
    );
}
